.button {
  padding: 13.5px 27px;
  background: #E91E63;
  color: #ffffff;
  text-decoration: none;
  display: inline-block;
  margin: 0;
  position: relative;
  transition: ease all .3s; }
  .button:hover {
    transform: scale(1.05);
    background: #aa1145;
    text-decoration: none; }
  .button:after {
    content: '';
    position: absolute;
    right: -22px;
    background: inherit;
    height: 48px;
    width: 48px;
    top: 0;
    border-radius: 100%; }
  .button:before {
    content: '';
    position: absolute;
    left: -22px;
    background: inherit;
    height: 48px;
    width: 48px;
    top: 0;
    border-radius: 100%; }

[class^="icon-"] span,
[class*=" icon-"] span {
  position: absolute;
  left: -9999px; }

